/* Elevations */
.elevated-bevel {
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.elevated-paper{
    box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%), 0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}
.elvated-cute {
    box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
}
.elevated-all{
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
}
.elevated-card{
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
}
.elevated-blend{
    box-shadow: 0 1px 0 0 transparent, 0 2px 10px 0 rgb(0 0 0 / 10%);
 }
.elevated-light{
    box-shadow: rgb(0 0 0 / 2%) 0px 1px 3px 0px, rgb(27 31 35 / 15%) 0px 0px 0px 1px;
}
.pop-up-shadow{
    box-shadow:0 2px 5px 0 rgb(0, 0, 0, 0.26), 0 2px 10px 0 rgb(0, 0, 0, 0.16);
}
.z-depth{
    box-shadow: 0 4px 17px 0 rgb(0 0 0 / 10%), 0 6px 17px 0 rgb(0 0 0 / 10%);
    -webkit-box-shadow: 0 4px 17px 0 rgb(0 0 0 / 10%), 0 6px 17px 0 rgb(0 0 0 / 10%);
}
.nice-shadow{
   box-shadow: 0 1px 0 0 transparent, 0 2px 10px 0 rgb(0 0 0 / 10%);
}

