/* ANIMATION CLASSES */
/* animation: name duration timing-function delay iteration-count direction fill-mode play-state; */
/* animations */
@keyframes bounce-z {
    0% {transform: scale(1);}
    50% {transform: scale(1.2);}
    100% {transform: scale(1);}
  }
@keyframes left-right {
    0% {left:-10%}
    100% {left:220%;}
    /* 100% {left:0%} */
  }
  .left-right{
    animation-name: left-right;
    animation-duration: 14s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
  }
@keyframes right-left {
    0% {left:440%;}
    100% {left:-120%}
    /* 100% {left:0%} */
  }
  .right-left{
    left:240%;
    animation-name: right-left;
    animation-duration: 14s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
  }


@keyframes bounce-z {
    0% {transform: scale(1);}
    50% {transform: scale(1.2);}
    100% {transform: scale(1);}
  }
  @keyframes bounce-x {
        0% {transform: translate(0px,0px); opacity: 1;}
        50% {transform: translate(7px,0px)}
        100% {transform: translate(0px,0px);opacity: 1;}
    }
    @keyframes zoom {
        0% {transform: scale(0.5);}
        50% {transform: scale(1.2);}
        100% {transform: scale(1);}
      }
    @keyframes pop-up-rise {
        0% {transform: translate(0px,0); opacity: 0.4;}
        /* 50% {transform: translate(0px,3px)} */
        100% {transform: translate(0px,-4px);opacity: 1;}
      }
    @keyframes slide-up {
        0% {margin-top: 10%;}
        /* 50% {transform: translate(0px,3px)} */
        100% {margin-top: 20px}
      }
    @keyframes slide-up-2 {
        0% {margin-bottom: 0%;}
        /* 50% {transform: translate(0px,3px)} */
        100% {margin-bottom: 17%}
      }
    @keyframes slide-up-slow {
         from {transform: translateX(0%);}
        to {transform: translateX(40%);}
      }
    @keyframes  slide-down-vanish {
        0% {transform: translate(0px,-12px);opacity: 1;}
        /* 50% {transform: translate(0px,3px)} */
        100% {transform: translate(0px,0); opacity: 0.4;}
      }
    @keyframes  hide-right {
        from{transform: translate(0px,0px);opacity: 1;}
        to{transform: translate(530px,0px);opacity: 0.4;}
      }
    @keyframes  rotate {
        from{transform:rotateZ(0deg) ;opacity: 1;}
        to{transform:rotateZ(360deg);opacity: 1;}
      }
    @keyframes  rotate2 {
        from{transform:rotateZ(0deg) ;opacity: 1;}
        to{transform:rotateZ(360deg);opacity: 1;}
      }
    @keyframes  reveal-right {
        from{transform: translate(0px,0px);opacity: 1;}
        to{transform: translate(-530px,0px);opacity: 1;}
      }
    @keyframes  fade-in {
        from{opacity: 0.3;}
        to{opacity: 1}
      }
