.formgenerator_pure-material-radio__2pQwG {
    z-index: 0;
    position: relative;
    display: inline-block;
    color: rgba(0, 0, 0, 0.87);
    color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.87);
    font-family: "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui, -apple-system;
    font-family: var(--pure-material-font, "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui, -apple-system);
    font-size: 16px;
    line-height: 1.5;
}

/* Input */
.formgenerator_pure-material-radio__2pQwG > input {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    z-index: -1;
    position: absolute;
    left: -10px;
    top: -8px;
    display: block;
    margin: 0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: rgba(0, 0, 0, 0.6);
    background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);
    outline: none;
    opacity: 0;
    transform: scale(1);
    pointer-events: none;
    transition: opacity 0.3s, transform 0.2s;
}

/* Span */
.formgenerator_pure-material-radio__2pQwG > span {
    display: inline-block;
    width: 100%;
    cursor: pointer;
}

/* Circle */
.formgenerator_pure-material-radio__2pQwG > span::before {
    content: "";
    display: inline-block;
    box-sizing: border-box;
    margin: 2px 10px 2px 0;
    border: solid 2px; /* Safari */
    border-color: rgba(0, 0, 0, 0.6);
    border-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    vertical-align: top;
    transition: border-color 0.2s;
}

/* Check */
.formgenerator_pure-material-radio__2pQwG > span::after {
    content: "";
    display: block;
    position: absolute;
    top: 2px;
    left: 0;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background-color: rgb(33, 150, 243);
    background-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));
    transform: translate(5px, 5px) scale(0);
    transition: transform 0.2s;
}

/* Checked */
.formgenerator_pure-material-radio__2pQwG > input:checked {
    background-color: rgb(33, 150, 243);
    background-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));
}

.formgenerator_pure-material-radio__2pQwG > input:checked + span::before {
    border-color: rgb(33, 150, 243);
    border-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));
}

.formgenerator_pure-material-radio__2pQwG > input:checked + span::after {
    transform: translate(5px, 5px) scale(1);
}

/* Hover, Focus */
.formgenerator_pure-material-radio__2pQwG:hover > input {
    opacity: 0.04;
}

.formgenerator_pure-material-radio__2pQwG > input:focus {
    opacity: 0.12;
}

.formgenerator_pure-material-radio__2pQwG:hover > input:focus {
    opacity: 0.16;
}

/* Active */
.formgenerator_pure-material-radio__2pQwG > input:active {
    opacity: 1;
    transform: scale(0);
    transition: transform 0s, opacity 0s;
}

.formgenerator_pure-material-radio__2pQwG > input:active + span::before {
    border-color: rgb(33, 150, 243);
    border-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));
}

/* Disabled */
.formgenerator_pure-material-radio__2pQwG > input:disabled {
    opacity: 0;
}

.formgenerator_pure-material-radio__2pQwG > input:disabled + span {
    color: rgba(0, 0, 0, 0.38);
    color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.38);
    cursor: auto;
    cursor: initial;
}

.formgenerator_pure-material-radio__2pQwG > input:disabled + span::before {
    border-color: currentColor;
}

.formgenerator_pure-material-radio__2pQwG > input:disabled + span::after {
    background-color: currentColor;
}

